import { useState } from "react";
import Login from "../Login/Login";
import Button from "../UI/Button";
import styles from "./MainPage.module.css"
import SignUp from "./SignUp";
const MainPage = (props) => {
    const [ShowSignUp , setShowSignUp] = useState(false);
    const [ShowSignIn , setShowSignIn] = useState(false);
    const signUpHandler = () => {
        setShowSignUp(!ShowSignUp)
    }
    const signInHandler = ()=> {
        setShowSignIn(true);

    }
    document.body.style.marginTop = "6rem"
     return (
         <div className={styles.flex_container}>
         <div className = {styles.ActionButton}>
            {!ShowSignUp && !ShowSignIn && <Button onClick={signUpHandler} className={styles.SignUp}>SignUp</Button>}
            {!ShowSignUp && !ShowSignIn && <Button onClick={signInHandler} className={styles.SignIn}>SignIn</Button>}
            {ShowSignUp && <SignUp  onSignUp={signUpHandler}/>}
            {ShowSignIn && <Login onLoggin={props.onSignIn} />}
         </div>
         </div>
     )
}
export default MainPage;